import React, { Component } from 'react';
import { Client } from '@petfinder/petfinder-js';
import Routes from './Routes';
import Navbar from './Navbar';
import './App.css';

class App extends Component{
  
  constructor(props){
    super(props);
    this.state = {
      dogs: []
    }
  }

  componentDidMount(){ 
        this.getDogs();
  }

  getDogs = () => {
    const client = new Client({apiKey: "aYkEPGGl0yVpNEu51e6NHlAVvHBtvUDdzZNkBqGx8ycXhzZTFM", secret:"UVn3DKS4Ne7jtUZdUcoY2uHRLwTIRpNX4DXaYqR0"});
    let dogs = [];
    client.animal.search({
      type: '',
      location: "New York, Ny",
      limit: 25
      })
      .then((response) => {
        console.log(response)
        response.data.animals.map(dog => (
          dogs.push({ 
            id: dog.id, 
            name: dog.name, 
            age: dog.age, 
            status: dog.status, 
            description: dog.description, 
            photo: dog.primary_photo_cropped.large, 
            gender: dog.gender,
            size: dog.size,
            url: dog.url
          })  
        )
        )
        this.setState(
          { dogs: dogs}
          ) 
      }).catch((e) => {
          alert(e)
      })
     
    }
  render(){
    return (

        <div>
          <Navbar dogs={this.state.dogs} /> 
          <div className="container">
            <Routes dogs={this.state.dogs}/>
          </div> 
        </div>
    );
  }
}

export default App;
