import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './DogList.css';

class DogList extends Component {
    render() {
        return(
            <div className="DogList">
                <h1 className="display-1 text-center mt-4 mb-4"><i className="em em-heartpulse"  aria-label="GROWING HEART"></i>Adopt!<i className="em em-heartpulse"  aria-label="GROWING HEART"></i></h1>    
                    <div className="row">
                        {this.props.dogs.map(dog => (
                            <div className="Dog col-md-6 col-lg-4 col-xl-3 text-center" key={dog.name}>
                                <Link  to={`/${dog.name}`} >
                                    <img className="rounded-circle" src={dog.photo} alt={dog.name} />
                                    <h3 className="Underline">
                                        {dog.name}
                                    </h3>
                                </Link>
                            </div>
                        ))}
                        
                    </div>
            </div>
        );  
    }
}

export default DogList;