import React, { Component } from 'react';
import './DogDetails.css';

class DogDetails extends Component {

    getDog = () => {
        let name = this.props.match.params.name;
        // console.log(this.props)
        let currentDog = [];
        
        this.props.dogs.map(
            dog => (dog.name.toLowerCase() === name.toLowerCase() && (currentDog = dog))
        );
                // console.log(currentDog)

        return currentDog;            
    }
    

    render(){
        // let name = this.props.match.params.name;
        let dog = this.getDog(this.props);
        console.log(dog)
        
        return (
        <div className="DogDetails row justify-content-center mt-5">
            <div className="col-11 col-lg-5">
                <div className="DogDetails-card card mb-5">
                    <img className="card-img-top" src={dog.photo} alt={dog.name} />
                    <div className="card-body">
                        <h2 className="card-title">
                            {dog.name}
                        </h2>
                        

                        <h4 className="card-subtitle text-muted">
                        Age: {dog.age} 
                        </h4>
                        <h4 className="mt-1 card-subtitle text-muted">
                        {dog.gender} 
                        </h4>
                        <h4 className="mt-1 card-subtitle text-muted">
                        Size: {dog.size} 
                        </h4>
                    </div>
                    <ul className="list-group list-group-flush">
                    
                            <li className="list-group-item">{dog.description}</li>
                    
                    </ul>
                    <div className="card-body">
                        <a href={dog.url} target="_blank" className="btn btn-info">Adopt Me</a>
                    </div>
                </div>  
            </div>
        </div>
        );
    }
}

export default DogDetails;